import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { EventbusService } from '@front-shared-lib';
import { runSubscriptionsForSharedUse } from '@main-shared-lib';

@Component({
  selector: 'app-admin-inviting-manager-modal',
  standalone: true,
  imports: [],
  templateUrl: './admin-inviting-manager-modal.component.html',
  styleUrl: './admin-inviting-manager-modal.component.scss'
})
export class AdminInvitingManagerModalComponent
implements OnInit, OnDestroy {
  @Input() showModal = false;
  @Input() showLoading = true;
  cleanup?: () => void;

  constructor(private eventbus: EventbusService) {}

  ngOnInit(): void {
    const handlers = {
      closeAdminModal: () => {
        this.showModal = false;
      }
    };

    const { cleanup } = runSubscriptionsForSharedUse(this.eventbus, handlers);

    this.cleanup = cleanup;
  }


  ngOnDestroy() {
    this.cleanup?.();
  }
}
